import React, { Component, Fragment } from 'react';
import {Header,Footer, Layout} from './UI/index';

class App extends Component {
  render() {
    return (
      <div>
        <Header />
        <main >
          <h3>Bienvenido a SGI</h3>
          <Layout />
        </main>
        <Footer />
      </div>
    );
  }
}

export default App;



